<template>
  <header class="header" :class="{ 'header-fix': isFixed }">
    <ul @click="tabClick" class="tabList flex po-r">
      <li :class="{ selectedTab: $store.state.pageTabId == tab.id }" v-for="tab in tabList" :key="tab.id"
        :data-id="tab.id" :data-url="tab.url">
        {{ tab.title }}
      </li>
    </ul>
  </header>
</template>

<script>
export default {
  name: "HeaderComponent",
  props: {
    msg: String,
  },
  data() {
    return {
      tabList: [
        { id: 1, title: "首页", url: "/" },
        { id: 2, title: "NIUNIU故事", url: "/#story" },
        { id: 3, title: "全明星计划", url: "/#about" },
        { id: 4, title: "产品下载", url: "/product" },
        { id: 5, title: "人才招聘", url: "/job" },
      ],
      currentId: 1,
      isFixed: false,
    };
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll)
    this.handleScroll()
  },
  methods: {
    tabClick(e) {
      // this.$store.state.pageTabId = e.target.dataset.id;
      e.target.dataset.url && this.$router.push(e.target.dataset.url);
    },
    handleScroll() {
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      this.isFixed = scrollTop > 48
    }
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
  }
};
</script>

